import  { Component } from "react";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import '../css/App.css';
import trophy from '../img/trophy.png'
type MyProps = { 
    scoreBoard?: any,
    totalScoreBoard?: any,
};
type MyState = { 
};
const theme = createTheme({
    palette: {
        mode: "dark"
    }
});
class ScoreBoard extends Component <MyProps, MyState>{
    
    constructor(props: MyProps) {
        super(props);
        this.state = {
        };
    }
    
    render() {
        const { scoreBoard, totalScoreBoard } = this.props;
        const scores = scoreBoard ? scoreBoard.scores : totalScoreBoard;
        let length = 14;

        return (
            <ThemeProvider theme={theme}>
                <Card sx={{ width: 400, maxWidth: '100vw', margin: 1, boxShadow: '8px 10px 25px 1px #000000', borderRadius: '10px' }}>
                    <CardContent>
                        <Typography 
                            gutterBottom 
                            variant="h6" 
                            className="smile"
                            component="div">
                                <div >
                                    <span className = {scoreBoard ? 'roundHeader': (totalScoreBoard ? 'totalHeader': '')}> 
                                        {
                                        (scoreBoard && scores[0].score)
                                        ? 
                                        'Runde resultat'
                                        :
                                        (scoreBoard)
                                            ?
                                            'Levert tippekuppong'
                                            :
                                            ''
                                        }
                                        { 
                                        (totalScoreBoard)
                                            ? 
                                            'Toppliste'
                                            : 
                                            ''
                                        } 
                                    </span> 
                                </div>
                            </Typography>
                            { scores ? 
                                scores.map((userScore: any, index: number) => (  
                                    <Typography 
                                        key={userScore.userId}
                                        variant="body1" 
                                        color={(index === 0 && userScore.score) ? "#FFD700": ((index === 1 && userScore.score) ? "#C0C0C0": (index === 2 && userScore.score) ? "#CD7F32": 'nothing')}>
                                        <div className="scoreBoard">

                                            <div>
                                                { 
                                                    userScore.score ?
                                                    (index+1)+'. '
                                                    :
                                                    '' } {

                                                    userScore.userName.substring(0, length) + (userScore.userName.length >= length ? '...' : '')
                                                    }
                                                    
                                                    
                                               
                                            </div>
                                            {
                                                (scoreBoard && userScore.score && index === 0) 
                                                ? 
                                                    <div><img src={trophy} className="trophyLogo" alt="logo" /></div>
                                                : 
                                                    (totalScoreBoard && userScore.trophies.length > 0) 
                                                    ?
                                                        <div>
                                                            {userScore.trophies.map((t: any) => (  
                                                            <img key={t} src={trophy} className="trophyLogo" alt="logo" />
                                                        ))}
                                                        </div>
                                                    :
                                                        <div/>
                                            }

                                            <div>
                                            {userScore.score ? userScore.score : ''} 
                                            </div>
                                        </div>
                                    </Typography>
                                ))  : 
                                '' 
                            }
                    </CardContent>
                </Card>
            </ThemeProvider>
        );
    }
}

export default ScoreBoard;

